import { mapActions, mapGetters } from "vuex";

import Input from "@/components/input";
import QuestionView from "../question-view";

export default {
  name: "home",
  components: {
    "app-input": Input,
    "app-question-view": QuestionView,
  },
  props: [],
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["getQuestions"]),
  },
  mounted() {
    this.fetchQuestions();
  },
  methods: {
    ...mapActions(["fetchQuestions"]),
    onAsk() {
      this.$router.push("questions").catch(() => null);
    },
  },
};
